<template>
  <div class="user" v-if="user">
    <div class="user__left">
      <img :src="user.avatar_url" alt="" />
      <a :href="user.html_url" class="user__btn">ПОСЕТИТЬ</a>
    </div>
    <div class="user__right">
      <p>{{ user.login }}</p>
      <p>
        Репозиториев: <span>{{ user.public_repos }}</span>
      </p>
      <p>
        Создан: <span>{{ new Date(user .created_at).toLocaleDateString() }}</span>
      </p>
      <p>
        Подписщиков: <span>{{ user.followers }}</span>
      </p>
      <p>
        Подписок: <span>{{ user.following }}</span>
      </p>
    </div>
  </div>
  <div class="sort">
    <h2 class="sort__title">Сортировка</h2>
    <div class="sort__box">
      <button
        class="sort__box-btn"
        v-for="(btn, index) in btns"
        :key="index"
        :class="{active: isActive == index}"
        @click="sortRepos(index,btn.sortType)"
      > {{ btn.name }} </button>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  computed: {
    ...mapState(["user"]),
  },
  data() {
    return {
      btns: [{sortType: 'name' ,name: "ИМЯ" },
             {sortType: 'stargazers_count' ,name: "ЗВЕЗДЫ" },
             {sortType: 'created_at' ,name: "ДАТА" }],
      isActive: 0,
    };
  },
  methods:{
    ...mapMutations(['sort']),
    sortRepos(id, sortType){
      this.isActive = id
      this.sort(sortType)
    }
  }
};
</script>

<style lang="scss">
.sort{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px 0;
  gap: 10px;
  &__title{
    color: #3F3F3F;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: 120%;
    text-transform: capitalize;
  }
  &__box{
    background: #E0E0E0;
    border-radius: 4px;
    overflow: hidden;
    padding: 2px;
    
    &-btn{
      border: none;
      outline: none;
      padding: 6px 36px;
      color: #3F3F3F;
      font-size: 16px;
      background: transparent;
      font-weight: 700;
      line-height: 120%;
      text-transform: capitalize;
      border-radius: 4px;
      transition: .4s;
      cursor: pointer;
      &.active{
      background: #FFF; 
      }
    }
  }
}
</style>